import JsEncrypt from 'jsencrypt';
function RSAencrypt(pas) {
	let publicKey =
		'-----BEGIN PUBLIC KEY-----\nMIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQCpjjKwzQxDIGqVQhzPtISt+jj1\nlQs8OJUbT7cTkqG1fQuYecvkRkhG0cKGbx157ZdfYrk8ATLvT12jQLBPlhb5KUkc\ntCtD3ErENKfoZ6m7ph78+ngMZ1y1tY2QYv9xxO8XGVg0GR5NN4z1ZLNpTHbLtjxu\nU1VLQKHKtk4RFA0/iwIDAQAB\n-----END PUBLIC KEY-----\n';
	if (!publicKey || !pas) {
		return pas;
	}
	// 实例化jsEncrypt对象
	let jse = new JsEncrypt();
	// 设置公钥
	jse.setPublicKey(publicKey);
	return jse.encrypt(pas);
}
import { getLang } from '@/utils/i18n';
const formatTime = (timestamp) => {
	let date = new Date(Number(timestamp + '000'));
	const year = date.getFullYear();
	const month = date.getMonth() + 1;
	const day = date.getDate();
	// const hour = date.getHours()
	// const minute = date.getMinutes()
	// const second = date.getSeconds()

	return [year, month, day].map(formatNumber).join('-');
	// return [year, month, day].map(formatNumber).join('-') + ' ' + [hour, minute, second].map(formatNumber).join(':')
};
const formatTime2 = (timestamp) => {
	if (!timestamp) {
		return '';
	}
	let date = new Date(Number(timestamp + '000'));
	const year = date.getFullYear();
	const month = date.getMonth() + 1;
	const day = date.getDate();
	const hour = date.getHours();
	const minute = date.getMinutes();
	const second = date.getSeconds();
	let result =
		year +
		'-' +
		month +
		'-' +
		day +
		'-' +
		' ' +
		[hour, minute, second].map(formatNumber).join(':');
	if (getLang() === 'zh-TW') {
		result =
			year +
			'年' +
			month +
			'月' +
			day +
			'日' +
			' ' +
			[hour, minute, second].map(formatNumber).join(':');
	}
	return result;
};

const formatNumber = (n) => {
	n = n.toString();
	return n[1] ? n : '0' + n;
};

export { RSAencrypt, formatTime, formatTime2 };
