import Web3 from 'web3';
import store from '../store/index';
import { Toast } from 'vant';
import { i18n } from './i18n';
import { isProd } from './config';
import { count } from "../storage"
count.set(0)
const web3Data = {
	web3: {},
	t: i18n.global.t,
	factoryContract: {},
	chainInfo: {
		chainId: isProd ? 56 : 97,
		chainName: isProd
			? 'Binance Smart Chain Mainnet'
			: 'Binance Smart Chain Testnet',
		rpcUrls: ['https://bsc-dataseed.binance.org'],
		blockExplorerUrls: ['https://bscscan.com/']
	},
	web3Provider: null,
	getBlance: (account) => {
		web3Data.web3.eth.getBalance(account).then((amount) => {
			store.commit(
				'updateCurrentBalance',
				web3Data.web3.utils.fromWei(amount, 'ether')
			);
		});
	},
	initWeb3: async () => {
		if (window.ethereum) {
			web3Data.web3Provider = window.ethereum;
			window.ethereum.on('accountsChanged', (accounts) => {
				store.commit('updateAccount', accounts);
			});
			web3Data.web3 = new Web3(web3Data.web3Provider);
			try {
				await window.ethereum.enable();
			} catch (error) {
				Promise.reject(error);
			}
		} else {
			if (window.web3) {
				web3Data.web3Provider = window.web3.currentProvider;
			} else {
				let num = count.get() || 0;
				if (Number(num) < 5) {
					count.set(Number(num) + 1);
					setTimeout(() => {
						web3Data.initWeb3();
					}, 500);
				} else {
					Toast(
						web3Data.t(
							'暫未檢測到錢包插件，請安裝MetaMask錢包，或在imtoken裡打開'
						)
					);
				}
			}
		}
		if (web3Data.web3Provider) {
			web3Data.web3 = new Web3(web3Data.web3Provider);
			web3Data.changeNetwork();
		}
		store.commit('updateProvider', web3Data.web3);
		web3Data.web3.eth.getAccounts().then((accounts) => {
			store.commit('updateAccount', accounts);
			return web3Data.getBlance(accounts[0]);
		});
	},
	changeNetwork: async function () {
		let id = await web3Data.web3.eth.net.getId();
		if (id != web3Data.chainInfo.chainId) {
			Toast(web3Data.t('web3.請將網路切換到幣安智慧鏈主網路。'));
			web3Data.switchChain();
		} else {
			web3Data.toConnect();
		}
	},
	switchChain: async function () {
		if (window.ethereum) {
			try {
				await web3Data.web3Provider
					.request({
						method: 'wallet_switchEthereumChain',
						params: [
							{ chainId: `0x${web3Data.chainInfo.chainId.toString(16)}` }
						]
					})
					.then(() => {
						web3Data.toConnect();
					});
			} catch (error) {
				if (error.code === 4902) {
					await web3Data.addChain();
				} else {
					Toast(error.message);
				}
			}
		} else {
			Toast(
				web3Data.t('web3.無法在Metamask上設定BSC網路，因為ethereum未定義。')
			);
		}
	},
	addChain: async function () {
		try {
			const data = [
				{
					chainId: `0x${web3Data.chainInfo.chainId.toString(16)}`,
					chainName: web3Data.chainInfo.chainName,
					nativeCurrency: {
						name: 'BNB',
						symbol: 'BNB',
						decimals: 18
					},
					rpcUrls: web3Data.chainInfo.rpcUrls,
					blockExplorerUrls: web3Data.chainInfoblockExplorerUrls
				}
			];
			/* eslint-disable */
			await web3Data.web3Provider.request({
				method: 'wallet_addEthereumChain',
				params: data
			});
		} catch (error) {
			Toast(web3Data.t(error.message));
		}
	},
	toConnect: function () {
		console.log('toConnect');
		store.commit('updateProvider', web3Data.web3);
		web3Data.web3Provider.on('connect', async (info) => {
			console.log('connect', info);
		});
		web3Data.web3Provider.on('accountsChanged', (accounts) => {
			// let acc = web3Data.web3.utils.toChecksumAddress(accounts[0])
			// store.commit("updateAccount", acc)
			window.location.reload()
		});
		web3Data.web3Provider.on('chainChanged', (chainId) => {
			if (chainId != `0x${web3Data.chainInfo.chainId.toString(16)}`) {
				store.commit('updateAccount', '');
				Toast(web3Data.t('web3.請將網路切換到幣安智慧鏈主網路。'));
			} else {
				store.commit('updateProvider', web3Data.web3);
				web3Data.web3.eth.getAccounts().then((accounts) => {
					let acc = web3Data.web3.utils.toChecksumAddress(accounts[0]);
					store.commit('updateAccount', acc);
				});
			}
		});
		web3Data.web3Provider.on('disconnect', (code, reason) => {
			console.log(code, reason);
		});
	}
};

export default web3Data;
